/* eslint-disable no-use-before-define */
import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { useContext, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { Redirect } from 'react-router-dom'
import Flexbox from 'components/Flexbox'
import { Formik, Form as FormikForm } from 'formik'
import { CHECKOUT_PRODUCTS } from 'constants/routes'
import { Page } from 'components'
import { checkoutContext } from 'providers/CheckoutProvider'
import Switch from 'react-switch'
import styled from 'styled-components'
import { StepTitle, Actions, Wrapper, StepTracker } from '../common'
import useForm from './useForm'
import Form from './Form'

const CheckoutDetails = () => {
  const { t } = useTranslation('checkout')
  const { checkout } = useContext(checkoutContext)

  useDocumentTitle(t('checkoutDetails.tabTitle'))
  useScrollTop()

  const [isIndividual, setIsIndividual] = useState(
    checkout.customerType === 'INDIVIDUAL' || false
  )

  const { onSubmitForm, initialValues, getSchema } = useForm()

  if (!checkout.products) {
    return <Redirect to={CHECKOUT_PRODUCTS} />
  }

  return (
    <Page>
      <StepTracker />

      <Wrapper>
        <StepTitle>{t('checkoutDetails.title')}</StepTitle>

        <Flexbox
          marginBottom="m-size"
          justifyContent="center"
          alignItems="center"
        >
          <CustomTypeButton onClick={() => setIsIndividual(false)}>
            {t('checkoutDetails.customerTypes.COMPANY')}
          </CustomTypeButton>

          <Switch
            offColor="#e1e1e1"
            onColor="#e1e1e1"
            checkedIcon={false}
            uncheckedIcon={false}
            onChange={(checked) => setIsIndividual(checked)}
            checked={isIndividual}
          />

          <CustomTypeButton onClick={() => setIsIndividual(true)}>
            {t('checkoutDetails.customerTypes.INDIVIDUAL')}
          </CustomTypeButton>
        </Flexbox>

        <Formik
          initialValues={initialValues}
          validateOnChange
          validationSchema={getSchema(isIndividual)}
          onSubmit={(form) => onSubmitForm(form, isIndividual)}
        >
          {({ handleSubmit }) => (
            <FormikForm onSubmit={handleSubmit}>
              <Form isIndividual={isIndividual} />
              <Actions />
            </FormikForm>
          )}
        </Formik>
      </Wrapper>
    </Page>
  )
}

const CustomTypeButton = styled.button`
  width: 50%;
  height: 5rem;
  background: none;
  border: none;
  font-size: 1.4rem;
  letter-spacing: 0.1rem;
`

export default CheckoutDetails
