/* eslint-disable implicit-arrow-linebreak */
import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Logo } from 'components'
import BasketToggle from './BasketToggle'
import MainMenu from './MainMenu'

const Navigation = () => {
  const [isExpanded, setIsExpanded] = useState(false)

  useEffect(() => {
    const scrollHandler = () => {
      if (window.pageYOffset >= 70) {
        setIsExpanded(true)
      } else {
        setIsExpanded(false)
      }
    }

    window.addEventListener('scroll', scrollHandler)

    return () => window.removeEventListener('scroll', scrollHandler)
  }, [])

  return (
    <Container isExpanded={isExpanded}>
      <Logo marginRight="m-size" />

      <MainMenu />

      <NavigationMenu>
        <NavigationMenuItem>
          <BasketToggle />
        </NavigationMenuItem>
      </NavigationMenu>
    </Container>
  )
}

type ContainerProps = {
  isExpanded: boolean
}

const Container = styled.nav<ContainerProps>`
  width: 100%;
  height: ${(props) => (props.isExpanded ? '6rem' : '10rem')};
  background: ${(props) =>
    props.theme.colors[props.isExpanded ? 'nav-bg-scrolled' : 'nav-bg']};
  display: flex;
  align-items: center;
  padding: 0.5rem 5rem;
  padding-top: ${(props) => (props.isExpanded ? '0.5rem' : '3rem')};
  position: ${(props) => (props.isExpanded ? 'fixed' : 'absolute')};
  top: 0;
  transform: translateY(0);
  z-index: ${(props) => props.theme.zIndices.navigation};
  box-shadow: ${(props) =>
    props.isExpanded && '0 5px 10px rgba(0, 0, 0, 0.02)'};
  /* @include bezier-transition(transform, 0.3s, ease); */
`

const NavigationMenu = styled.ul`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0;
  margin: 0;
  text-align: right;
`

export const NavigationMenuItem = styled.li`
  display: inline-block;
  list-style-type: none;
  padding: 0;
  margin: 0;
`

export default Navigation
