import React, { ReactNode } from 'react'
import styled from 'styled-components'
import { Flexbox } from 'components'
import Box from 'components/Box'
import { SpaceProps } from 'styled-system'

export type BasicProps = {
  title: ReactNode
  description?: string
  leftChildren?: ReactNode
} & SpaceProps

type Props = BasicProps & {
  small?: boolean
  children?: ReactNode
}

const Banner = ({
  small,
  description,
  title,
  children,
  leftChildren,
  ...props
}: Props) => (
  <Flexbox
    {...props}
    height={small ? '20rem' : '40rem'}
    backgroundColor="color1"
  >
    <Description fullWidth={!children}>
      {title}
      {description && <Box as="p">{description}</Box>}
      {leftChildren}
    </Description>

    <Flexbox marginX="auto" alignItems="center">
      {children}
    </Flexbox>
  </Flexbox>
)

type DescriptionProp = {
  fullWidth: boolean
}
const Description = styled.div<DescriptionProp>`
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  padding: 5rem;
  flex-basis: ${(props) => (props.fullWidth ? '100%' : '50%')};

  h1 {
    font-size: 4.8rem;
    width: 80%;
  }
`

export default Banner
