/* eslint-disable no-use-before-define */
import { IconName } from '@fortawesome/free-solid-svg-icons'
import { Page, Banner, Grid, Box } from 'components'
import { useDocumentTitle, useScrollTop } from 'hooks'

import React, { ReactNode } from 'react'
import { useTranslation } from 'react-i18next'
import CheckStatus from './CheckStatus'

import Tile from './Tile'

const Contact = () => {
  const { t } = useTranslation('contact')

  useScrollTop()
  useDocumentTitle(t('tabTitle'))

  const contactMethods = useContactMethods()

  return (
    <Page>
      <Banner small title={<h1>{t('title')}</h1>} />
      <Grid gridTemplateColumns="repeat(3, 1fr)" marginTop="3rem">
        {contactMethods.map(({ key, ...props }) => (
          <Tile key={key} title={t(`items.${key}.title`)} {...props} />
        ))}
      </Grid>
    </Page>
  )
}

type ContactMethod = {
  icon: IconName
  key: string
  extended?: boolean
  content: ReactNode
}

const useContactMethods = (): ContactMethod[] => {
  const { t } = useTranslation('contact')

  return [
    {
      // icon: 'box',
      key: 'CHECK_STATUS',
      extended: true,
      content: <CheckStatus />
    },

    {
      icon: 'envelope-open-text',
      key: 'EMAIL',
      content: (
        <>
          <Box marginTop={0} as="h5">
            {t('items.EMAIL.info')}
          </Box>
          <Box as="h5" color="paragraph-color" marginTop="0">
            {t('items.EMAIL.email')}
          </Box>
        </>
      )
    },
    {
      icon: 'phone-alt',
      key: 'PHONE',
      content: (
        <>
          <Box marginTop={0} as="h5">
            {t('items.PHONE.info')}
          </Box>
          <Box as="h5" color="paragraph-color" marginTop="0">
            {t('items.PHONE.mobile')}
          </Box>
        </>
      )
    },
    {
      icon: 'map-marked-alt',
      key: 'ADDRESS',
      content: (
        <>
          <Box marginTop={0} as="h5">
            {t('items.ADDRESS.info')}
          </Box>
          <Box as="h5" color="paragraph-color" marginY="0">
            {t('items.ADDRESS.address1')}
          </Box>
          <Box as="h5" color="paragraph-color" marginY="0">
            {t('items.ADDRESS.address2')}
          </Box>
          <Box as="h5" color="paragraph-color" marginY="0">
            {t('items.ADDRESS.address3')}
          </Box>
          <Box as="h5" color="paragraph-color" marginTop="0">
            {t('items.ADDRESS.address4')}
          </Box>
        </>
      )
    },

    {
      icon: 'university',
      key: 'ACCOUNT',
      content: (
        <>
          <Box marginTop={0} as="h5">
            {t('items.ACCOUNT.info')}
          </Box>
          <Box as="h5" color="paragraph-color" marginTop="0">
            {t('items.ACCOUNT.nrb')}
          </Box>
          <Box marginY={0} as="h5">
            {t('items.ACCOUNT.info2')}
          </Box>
          <Box marginTop={0} as="h5">
            {t('items.ACCOUNT.info3')}
          </Box>
        </>
      )
    }
  ]
}

export default Contact
