import { Form as FormikForm, Formik } from 'formik'
import React from 'react'
import { Box, Flexbox } from 'components'
import { useTranslation } from 'react-i18next'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Form from './Form'
import Result from './Result'
import Error from './Error'
import useForm from './useForm'

const CheckStatus = () => {
  const { t } = useTranslation('contact')
  const { view, setView, schema, initialValues, onSubmit } = useForm()

  if (view.view === 'RESULT') {
    return <Result setView={setView} status={view.status} />
  }

  if (view.view === 'ERROR') {
    return <Error setView={setView} message={view.message} />
  }

  return (
    <Formik
      initialValues={initialValues}
      validateOnChange
      validationSchema={schema}
      onSubmit={onSubmit}
    >
      {({ handleSubmit }) => (
        <FormikForm onSubmit={handleSubmit}>
          <Flexbox justifyContent="space-between">
            <Flexbox flexDirection="column">
              <Box marginTop={0} as="h5">
                {t('items.CHECK_STATUS.info')}
              </Box>

              <Form />
            </Flexbox>

            <Flexbox alignItems="center" justifyContent="center">
              <FontAwesomeIcon icon="box" size="7x" />
            </Flexbox>
          </Flexbox>
        </FormikForm>
      )}
    </Formik>
  )
}
export default CheckStatus
