/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-confusing-arrow */
/* eslint-disable jsx-a11y/control-has-associated-label */
/* eslint-disable react/forbid-prop-types */
import { useGetOrders } from 'api/getOrders'
import { Box, QueryLoader } from 'components'
import isBefore from 'date-fns/isBefore'
import { OrderStatus } from 'models/order'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { getDateFromTimestamp } from 'utils'
import Row from './Row'

type Props = {
  status: OrderStatus
}

const Table = ({ status }: Props) => {
  const { t } = useTranslation('adminOrders')
  const ordersQuery = useGetOrders(status)

  return (
    <QueryLoader query={ordersQuery}>
      {(orders) => (
        <Box
          as="table"
          width="100%"
          border="1px solid"
          borderColor="border-color"
        >
          {orders.length > 0 && (
            <thead>
              <tr>
                <th>
                  <h5>{t('headers.id')}</h5>
                </th>
                <th>
                  <h5>{t('headers.modifiedAt')}</h5>
                </th>
                <th>
                  <h5>{t('headers.createdAt')}</h5>
                </th>
                <th>
                  <h5>{t('headers.clientType')}</h5>
                </th>
                <th>
                  <h5>{t('headers.deliveryType')}</h5>
                </th>
                <th>
                  <h5>{t('headers.total')}</h5>
                </th>

                <th>
                  <h5>{t('headers.status')}</h5>
                </th>
              </tr>
            </thead>
          )}

          <tbody>
            {orders
              .sort((prev, next) => {
                if (
                  isBefore(
                    getDateFromTimestamp(prev.modifiedAt),
                    getDateFromTimestamp(next.modifiedAt)
                  )
                ) {
                  return -1
                }

                return 1
              })
              .map((order) => (
                <Row key={order.id} order={order} />
              ))}
            {orders.length === 0 && (
              <tr>
                <td>{t('emptyState')}</td>
              </tr>
            )}
          </tbody>
        </Box>
      )}
    </QueryLoader>
  )
}

export default Table
