import Flexbox from 'components/Flexbox'
import React, { ReactNode } from 'react'
import {
  AlignItemsProps,
  FlexDirectionProps,
  GridColumnProps,
  HeightProps,
  JustifyContentProps,
  SpaceProps
} from 'styled-system'

type Props = {
  children?: ReactNode
  onClick?: () => void
} & SpaceProps &
  FlexDirectionProps &
  JustifyContentProps &
  HeightProps &
  AlignItemsProps &
  GridColumnProps

const Tile = ({ onClick, children, ...props }: Props) => (
  <Flexbox
    width="100%"
    padding="30px"
    background="white"
    border="1px solid"
    borderColor="border-color"
    flexDirection="column"
    onClick={onClick}
    {...props}
  >
    {children}
  </Flexbox>
)

export default Tile
