/* eslint-disable implicit-arrow-linebreak */
import {
  collection,
  getDocs,
  getFirestore,
  query,
  where
} from 'firebase/firestore'
import { product } from 'models'
import { useQuery } from 'react-query'
import { getCollection } from 'utils'

type GetProductsPayload = { tag?: string }
const getProducts = async (payload: GetProductsPayload) => {
  const db = getFirestore()
  const ref = collection(db, 'products')

  let q = query(ref)
  if (payload.tag) {
    q = query(ref, where('tags', 'array-contains', payload.tag))
  }

  const snapshot = await getDocs(q)
  const products = getCollection(snapshot, product)

  return products
}

export const useGetProducts = (tag?: string) =>
  useQuery(`products${tag ? `/${tag}` : ''}`, () => getProducts({ tag }))

export default getProducts
