import { Form as FormikForm, Formik } from 'formik'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { useContext } from 'react'
import { Redirect } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { Page } from 'components'
import { CHECKOUT_DELIVERY } from 'constants/routes'
import { checkoutContext } from 'providers/CheckoutProvider'
import { StepTracker, Actions, StepTitle, Total, Wrapper } from '../common'
import useForm from './useForm'

import Form from './Form'

const CheckoutPayment = () => {
  const { t } = useTranslation('checkout')

  useDocumentTitle(t('checkoutPayment.tabTitle'))
  useScrollTop()

  const { checkout } = useContext(checkoutContext)

  const { onSubmit, initialValues, schema, isLoading } = useForm()

  if (!checkout.deliveryType) {
    return <Redirect to={CHECKOUT_DELIVERY} />
  }

  return (
    <Page>
      <StepTracker />

      <Wrapper>
        <StepTitle>{t('checkoutPayment.title')}</StepTitle>

        <Formik
          initialValues={initialValues}
          validateOnChange
          validationSchema={schema}
          onSubmit={onSubmit}
        >
          {({ handleSubmit }) => (
            <FormikForm onSubmit={handleSubmit}>
              <Form />
              <Total />
              <Actions primaryDisabled={isLoading} />
            </FormikForm>
          )}
        </Formik>
      </Wrapper>
    </Page>
  )
}

export default CheckoutPayment
