import { Flexbox, Page, Separator } from 'components'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { Fragment, useContext } from 'react'
import { useTranslation } from 'react-i18next'
import BasketItem from 'commonComponents/Basket/BasketItem'
import { basketContext } from 'providers/BasketProvider'
import { Actions, StepTracker, Total, Wrapper } from '../common'
import StepTitle from '../common/StepTitle'

const CheckoutProducts = () => {
  const { t } = useTranslation('checkout')

  const { basket, basketLength } = useContext(basketContext)

  useDocumentTitle(t('checkoutProducts.tabTitle'))
  useScrollTop()

  const isBasketEmpty = basketLength === 0

  return (
    <Page>
      <StepTracker />

      <Wrapper>
        <StepTitle>{t('checkoutProducts.title')}</StepTitle>

        <Flexbox
          minHeight="20rem"
          flexDirection="column"
          marginTop="2rem"
          alignItems="center"
        >
          {isBasketEmpty && (
            <Flexbox alignItems="center" justifyContent="center" flexGrow={1}>
              <h5 className="basket-empty-msg">
                {t('checkoutProducts.emptyState')}
              </h5>
            </Flexbox>
          )}

          {basket.map((product) => (
            <Fragment key={product.id}>
              <BasketItem product={product} />
            </Fragment>
          ))}
        </Flexbox>

        {!isBasketEmpty && <Total />}
        <Actions />
      </Wrapper>
    </Page>
  )
}

export default CheckoutProducts
