import Box from 'components/Box'
import Flexbox from 'components/Flexbox'
import { useHistory } from 'react-router-dom'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import { basketContext } from 'providers/BasketProvider'
import {
  CHECKOUT_DELIVERY,
  CHECKOUT_DETAILS,
  CHECKOUT_PRODUCTS,
  PRODUCTS
} from 'constants/routes'
import { checkoutContext } from 'providers/CheckoutProvider'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useCheckoutStep } from './StepTracker'

type Props = {
  primaryDisabled?: boolean
}

const Actions = ({ primaryDisabled }: Props) => {
  const { t } = useTranslation('checkout')
  const history = useHistory()

  const current = useCheckoutStep()

  const { basketLength, basket } = useContext(basketContext)
  const { setCheckout } = useContext(checkoutContext)
  const isBasketEmpty = basketLength === 0

  const onClickPrevious = () => {
    switch (current) {
      case 1:
        history.push(PRODUCTS)
        break

      case 2:
        history.push(CHECKOUT_PRODUCTS)
        break

      case 3:
        history.push(CHECKOUT_DETAILS)
        break

      case 4:
        history.push(CHECKOUT_DELIVERY)
        break

      case 5:
        history.push(CHECKOUT_DELIVERY)
        break

      default:
        break
    }
  }

  const isFirstPage = current === 1
  const onClickNext = () => {
    if (isFirstPage) {
      history.push(CHECKOUT_DETAILS)
      setCheckout((prev) => ({
        ...prev,
        products: basket
      }))
    }
  }

  return (
    <Flexbox
      marginTop="3rem"
      alignItems="center"
      justifyContent="space-between"
    >
      <button
        className="button button-muted"
        onClick={onClickPrevious}
        type="button"
      >
        {isFirstPage && (
          <Box marginRight="m-size">
            <FontAwesomeIcon icon="store-alt" />
          </Box>
        )}

        {t(isFirstPage ? 'actions.backToShop' : 'actions.back')}
      </button>
      <button
        disabled={isBasketEmpty || primaryDisabled}
        className="button"
        onClick={onClickNext}
        type={isFirstPage ? 'button' : 'submit'}
      >
        <Box marginRight="s-size">
          {t(current === 4 ? 'actions.createOrder' : 'actions.next')}
        </Box>

        <FontAwesomeIcon icon="arrow-right" />
      </button>
    </Flexbox>
  )
}

export default Actions
