/* eslint-disable no-undef */
/* eslint-disable no-use-before-define */
import { CheckboxField } from 'components'
import React from 'react'
import { Field, FieldProps } from 'formik'
import { useTranslation } from 'react-i18next'
import { displayMoney } from 'utils'
import { DeliveryType } from 'models/order'
import { DELIVERY_METHODS } from 'cms/config'
import { FormValues } from './useForm'
import { Row } from '../common'

const Form = () => {
  const { t: commonT } = useTranslation('common')
  const { t } = useTranslation('checkout')

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <Row flexDirection="column">
          {DELIVERY_METHODS.map((method, index) => (
            <Field name="deliveryType" key={method.name}>
              {(props: FieldProps<DeliveryType, FormValues>) => (
                <CheckboxField
                  {...props}
                  id={method.name}
                  isFirst={index === 0}
                  title={commonT(`deliveryMethods.${method.name}`)}
                  subtitle={`${method.time}`}
                  val={displayMoney(method.price)}
                  fieldLabel={t('checkoutDelivery.form.delivery.label')}
                />
              )}
            </Field>
          ))}
        </Row>
      </div>
    </div>
  )
}

export default Form
