/* eslint-disable no-use-before-define */
/* eslint-disable react/jsx-wrap-multilines */
import { useDocumentTitle, useScrollTop } from 'hooks'
import React from 'react'
import { Link } from 'react-router-dom'
import {
  Page,
  Box,
  QueryLoader,
  LongProductTile,
  SectionHead,
  Grid
} from 'components'

import { useTranslation } from 'react-i18next'
import bannerImg from 'assets/banner-guy.png'
import { useGetProducts } from 'api/getProducts'
import { PRODUCTS } from 'constants/routes'
import FeatureItem from 'components/FeatureItem'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import BannerWithPicture from 'components/Banner/BannerWithPicture'
import { IconName } from '@fortawesome/free-solid-svg-icons'

const Home = () => {
  const { t } = useTranslation('home')

  useDocumentTitle(t('tabTitle'))
  useScrollTop()

  const productsQuery = useGetProducts()

  return (
    <Page>
      <BannerWithPicture
        img={bannerImg}
        description={t('banner.description')}
        title={
          <Box marginBottom="0" as="h1" className="text-thin">
            <strong>See</strong>
            &nbsp;everything with&nbsp;
            <strong>Clarity</strong>
          </Box>
        }
      >
        <Link to={PRODUCTS} className="button">
          {t('banner.buttonLabel')}
          <Box marginLeft="m-size">
            <FontAwesomeIcon icon="arrow-right" />
          </Box>
        </Link>
      </BannerWithPicture>

      <SectionHead marginTop="3rem" title={t('features.title')} />
      <Grid>
        {features.map((feature) => (
          <FeatureItem
            key={feature.key}
            title={t(`features.items.${feature.key}`)}
          >
            <FontAwesomeIcon icon={feature.icon} size="3x" />
          </FeatureItem>
        ))}
      </Grid>

      <SectionHead
        marginTop="3rem"
        title={t('bestsellers.title')}
        link={{
          to: '/products',
          label: t('bestsellers.linkLabel')
        }}
      />
      <QueryLoader query={productsQuery}>
        {(products) => (
          <>
            {products.map((product, index) => (
              <LongProductTile
                key={product.id}
                product={product}
                marginTop={index === 0 ? 0 : '3rem'}
              />
            ))}
          </>
        )}
      </QueryLoader>
    </Page>
  )
}

const features: { key: string; icon: IconName }[] = [
  {
    key: 'FREE_DELIVERY',
    icon: 'truck'
  },
  {
    key: 'QUICK_SERVICE',
    icon: 'user-clock'
  },
  {
    key: 'RETURNS',
    icon: 'exchange-alt'
  },
  {
    key: 'QUALITY',
    icon: 'star'
  },
  {
    key: 'PICK_UP',
    icon: 'box-open'
  },
  {
    key: 'CLUB',
    icon: 'hand-holding-usd'
  }
]

export default Home
