import { Flexbox, Grid, QueryLoader, SectionHead } from 'components'
import { Product } from 'models/product'
import React from 'react'
import { UseQueryResult } from 'react-query'
import Tile from './Tile'

type Props = {
  query: UseQueryResult<Product[]>
  title?: string
}

const ProductsList = ({ title, query }: Props) => (
  <QueryLoader query={query}>
    {(products) => (
      <Flexbox flexDirection="column" as="section">
        <SectionHead title={title} />

        <Grid>
          {products.map((product) => (
            <Tile key={product.id} product={product} />
          ))}
        </Grid>
      </Flexbox>
    )}
  </QueryLoader>
)

export default ProductsList
