import React, { Fragment, useContext, useRef, useEffect } from 'react'
import { Flexbox } from 'components'

import { basketContext } from 'providers/BasketProvider'
import styled from 'styled-components'
import { basketToggleContext } from 'providers/BasketToggleProvider'
import { useTranslation } from 'react-i18next'
import { BasketItem as BasketItemType } from 'models/basketItem'
import Header from './Header'
import Checkout from './Checkout'
import BasketItem from './BasketItem'

const Basket = () => {
  const { t } = useTranslation('common')

  const { isOpen, setIsOpen } = useContext(basketToggleContext)
  const { basket, basketLength } = useContext(basketContext)
  const ref = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (ref && ref.current && !ref.current.contains(event.target)) {
        setIsOpen(false)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)

    return () => document.removeEventListener('mousedown', handleClickOutside)
  })

  return (
    <Container ref={ref} isOpen={isOpen}>
      <Header />

      <Flexbox
        flexGrow={1}
        flexDirection="column"
        overflowY="auto"
        padding="s-size"
      >
        {!basketLength && (
          <Flexbox alignItems="center" justifyContent="center" flexGrow={1}>
            <h5 className="basket-empty-msg">{t('basket.empty')}</h5>
          </Flexbox>
        )}

        {basket.map((product) => (
          <Fragment key={getBasketProductKey(product)}>
            <BasketItem product={product} />
          </Fragment>
        ))}
      </Flexbox>

      <Checkout />
    </Container>
  )
}

type ContainerProps = {
  isOpen: boolean
}

const Container = styled.div<ContainerProps>`
  width: 60rem;
  height: 100vh;
  background: ${(props) => props.theme.colors.white};
  position: fixed;
  top: 0;
  right: 0;
  /* transform: translateX(100%); */
  display: ${(props) => (props.isOpen ? 'flex' : 'none')};
  flex-direction: column;

  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.08);
  z-index: ${(props) => props.theme.zIndices.basket};
`

export const getBasketProductKey = (product: BasketItemType) =>
  `${product.id}${product.variant ? `_${product.variant}` : ''}`

export default Basket
