import React from 'react'
import { Field, FieldProps } from 'formik'
import { useTranslation } from 'react-i18next'
import { CheckboxField } from 'components'
import { PAYMENT_METHODS } from 'cms/config'
import { PaymentType } from 'models/order'
import { FormValues } from './useForm'
import { Row } from '../common'

const Form = () => {
  const { t: commonT } = useTranslation('common')
  const { t } = useTranslation('checkout')

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <Row flexDirection="column">
          {PAYMENT_METHODS.map((method, index) => (
            <Field name="paymentType" key={method}>
              {(props: FieldProps<PaymentType, FormValues>) => (
                <CheckboxField
                  {...props}
                  id={method}
                  isFirst={index === 0}
                  title={commonT(`paymentMethods.${method}.title`)}
                  subtitle={commonT(`paymentMethods.${method}.subtitle`)}
                  fieldLabel={t('checkoutPayment.form.paymentType.label')}
                />
              )}
            </Field>
          ))}
        </Row>
      </div>
    </div>
  )
}

export default Form
