import React, { ReactNode } from 'react'
import styled from 'styled-components'
import Banner, { BasicProps } from '.'

type Props = BasicProps & {
  img?: string
  children?: ReactNode
}

const BannerWithPicture = ({ children, img, ...props }: Props) => (
  <Banner leftChildren={children} {...props}>
    <ImageWrapper>
      <img src={img} alt="" />
    </ImageWrapper>
  </Banner>
)

const ImageWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  /* flex-basis: 100%; */
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* transform: translateX(5rem); */
  }
`
export default BannerWithPicture
