/* eslint-disable implicit-arrow-linebreak */
import { Box, Flexbox } from 'components'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'
import mainMenuLinks from './mainMenuLinks'

const MainMenu = () => {
  const { t } = useTranslation('common')

  return (
    <Flexbox as="ul" paddingLeft={0} marginRight="m-size" flexGrow={1}>
      {mainMenuLinks.map(({ label, ...link }) => (
        <Box display="inline-block" as="li" key={link.to}>
          <StyledNavLink {...link}>{t(label)}</StyledNavLink>
        </Box>
      ))}
    </Flexbox>
  )
}

const StyledNavLink = styled(NavLink)`
  opacity: 0.5;
  font-size: 1.4rem;
  padding: ${(props) =>
    `${props.theme.space['xs-size']} ${props.theme.space['m-size']}`};

  &:hover {
    background: ${(props) => props.theme.colors['background-color-01']};
  }
`

export default MainMenu
