/* eslint-disable no-undef */
/* eslint-disable no-use-before-define */
/* eslint-disable jsx-a11y/label-has-associated-control */
import { Input } from 'components'
import { Field, FieldProps } from 'formik'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { City, PostCode, Street } from 'old/types'
import { FormValues } from './useForm'
import FieldWrapper from '../common/FieldWrapper'
import Row from '../common/Row'

const Form = () => {
  const { t } = useTranslation('checkout')

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <Row>
          <FieldWrapper>
            <Field name="street">
              {(props: FieldProps<Street, FormValues>) => (
                <Input
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutShipping.form.street.label'),
                    true
                  )}
                  placeholder={t('checkoutShipping.form.street.placeholder')}
                />
              )}
            </Field>
          </FieldWrapper>
        </Row>

        <Row>
          <FieldWrapper>
            <Field name="postCode">
              {(props: FieldProps<PostCode, FormValues>) => (
                <Input
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutShipping.form.postCode.label'),
                    true
                  )}
                  placeholder={t('checkoutShipping.form.postCode.placeholder')}
                />
              )}
            </Field>
          </FieldWrapper>

          <FieldWrapper>
            <Field name="city">
              {(props: FieldProps<City, FormValues>) => (
                <Input
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutShipping.form.city.label'),
                    true
                  )}
                  placeholder={t('checkoutShipping.form.city.placeholder')}
                />
              )}
            </Field>
          </FieldWrapper>
        </Row>
      </div>
    </div>
  )
}

const getRequiredOrNot = (label: string, required?: boolean) =>
  `${required ? '* ' : ''}${label}`

export default Form
