import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import { Formik, Form as FormikForm } from 'formik'
import { Page } from 'components'
import { checkoutContext } from 'providers/CheckoutProvider'
import { Redirect } from 'react-router-dom'
import { CHECKOUT_DELIVERY } from 'constants/routes'
import { StepTracker, Wrapper, Actions, StepTitle } from '../common'

import useForm from './useForm'
import Form from './Form'

const CheckoutShipping = () => {
  const { t } = useTranslation('checkout')

  useDocumentTitle(t('checkoutShipping.tabTitle'))
  useScrollTop()

  const { checkout } = useContext(checkoutContext)

  const { onSubmitForm, initialValues, schema } = useForm()

  if (!checkout.deliveryType) {
    return <Redirect to={CHECKOUT_DELIVERY} />
  }

  return (
    <Page>
      <StepTracker />

      <Wrapper>
        <StepTitle>{t('checkoutShipping.title')}</StepTitle>

        <Formik
          initialValues={initialValues}
          validateOnChange
          validationSchema={schema}
          onSubmit={onSubmitForm}
        >
          {({ handleSubmit }) => (
            <FormikForm onSubmit={handleSubmit}>
              <Form />
              <Actions />
            </FormikForm>
          )}
        </Formik>
      </Wrapper>
    </Page>
  )
}

export default CheckoutShipping
