/* eslint-disable object-curly-newline */
/* eslint-disable no-use-before-define */
/* eslint-disable jsx-a11y/label-has-associated-control */
import { Field, FieldProps, FormikValues, useFormikContext } from 'formik'
import { getRequiredOrNot } from 'utils'
import React from 'react'
import { MobileInput, Input } from 'components'
import { useTranslation } from 'react-i18next'
import { City, Email, Fullname, Postcode, Street } from 'old/types'
import { Mobile } from 'models/order'
import { FormValues } from './useForm'
import Row from '../common/Row'
import FieldWrapper from '../common/FieldWrapper'

type Props = {
  isIndividual: boolean
}
const Form = ({ isIndividual }: Props) => {
  const { t } = useTranslation('checkout')
  const { values } = useFormikContext<FormikValues>()

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <Row>
          <FieldWrapper>
            <Field name="fullname" type="text">
              {(props: FieldProps<Fullname, FormValues>) => {
                const base = isIndividual ? 'name' : 'company'

                return (
                  <Input
                    {...props}
                    label={getRequiredOrNot(
                      t(`checkoutDetails.form.${base}.label`),
                      true
                    )}
                    placeholder={t(`checkoutDetails.form.${base}.placeholder`)}
                  />
                )
              }}
            </Field>
          </FieldWrapper>

          {!isIndividual && (
            <FieldWrapper>
              <Field name="nip" type="text">
                {(props: FieldProps<Fullname, FormValues>) => (
                  <Input
                    {...props}
                    label={getRequiredOrNot(
                      t('checkoutDetails.form.nip.label'),
                      true
                    )}
                    placeholder={t('checkoutDetails.form.nip.placeholder')}
                  />
                )}
              </Field>
            </FieldWrapper>
          )}
        </Row>

        <Row>
          <FieldWrapper>
            <Field name="street" type="text">
              {(props: FieldProps<Street, FormValues>) => (
                <Input
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutDetails.form.street.label'),
                    true
                  )}
                  placeholder={t('checkoutDetails.form.street.placeholder')}
                />
              )}
            </Field>
          </FieldWrapper>
        </Row>

        <Row>
          <FieldWrapper>
            <Field name="postCode" type="text">
              {(props: FieldProps<Postcode, FormValues>) => (
                <Input
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutDetails.form.postCode.label'),
                    true
                  )}
                  placeholder={t('checkoutDetails.form.postCode.placeholder')}
                />
              )}
            </Field>
          </FieldWrapper>

          <FieldWrapper>
            <Field name="city" type="text">
              {(props: FieldProps<City, FormValues>) => (
                <Input
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutDetails.form.city.label'),
                    true
                  )}
                  placeholder={t('checkoutDetails.form.city.placeholder')}
                />
              )}
            </Field>
          </FieldWrapper>
        </Row>

        <Row>
          <FieldWrapper>
            <Field name="email" type="email">
              {(props: FieldProps<Email, FormValues>) => (
                <Input
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutDetails.form.email.label'),
                    true
                  )}
                  placeholder={t('checkoutDetails.form.email.placeholder')}
                />
              )}
            </Field>
          </FieldWrapper>

          <FieldWrapper>
            <Field name="mobile">
              {(props: FieldProps<Mobile, FormValues>) => (
                <MobileInput
                  label={getRequiredOrNot(
                    t('checkoutDetails.form.phone.label'),
                    true
                  )}
                  fieldProps={props}
                  name="mobile"
                  defaultValue={values.mobile}
                />
              )}
            </Field>
          </FieldWrapper>
        </Row>
      </div>
    </div>
  )
}

export default Form
