/* eslint-disable no-undef */
import { useHistory } from 'react-router-dom'
import { object, string } from 'yup'
import { CHECKOUT_PAYMENT } from 'constants/routes'
import { useTranslation } from 'react-i18next'
import { useContext } from 'react'
import { checkoutContext } from 'providers/CheckoutProvider'
import { Shipping } from 'models/order'

export type FormValues = Shipping

const useForm = () => {
  const { t } = useTranslation('checkout')
  const history = useHistory()

  const { checkout, setCheckout } = useContext(checkoutContext)

  const onSubmitForm = (form: FormValues) => {
    history.push(CHECKOUT_PAYMENT)
    setCheckout((prev) => ({
      ...prev,
      shipping: form
    }))
  }

  const initialValues: FormValues = {
    street: checkout.shipping?.street ?? '',
    postCode: checkout.shipping?.postCode ?? '',
    city: checkout.shipping?.city ?? ''
  }

  const schema = object().shape({
    street: string().required(t('checkoutShipping.validation.street.required')),
    postCode: string()
      .trim()
      .matches(
        /^(\d{2})-(\d{3})$/,
        t('checkoutShipping.validation.postCode.matches')
      )
      .required(t('checkoutShipping.validation.postCode.required')),
    city: string().required(t('checkoutShipping.validation.city.required'))
  })

  return {
    onSubmitForm,
    initialValues,
    schema
  }
}

export default useForm
