/* eslint-disable no-use-before-define */
import { useHistory } from 'react-router-dom'

import { mixed, object } from 'yup'
import { PAYMENT_METHODS } from 'cms/config'
import { useTranslation } from 'react-i18next'
import { CHECKOUT_RESULT } from 'constants/routes'
import { useContext } from 'react'
import { checkoutContext, initState } from 'providers/CheckoutProvider'
import { useMutation } from 'react-query'
import { addOrder, triggerNewOrder } from 'api'
import { basketContext } from 'providers/BasketProvider'

import { Order, PaymentType } from 'models/order'
import { Timestamp } from 'firebase/firestore'

export type FormValues = {
  paymentType: PaymentType
}

const useFormSubmit = () => {
  const history = useHistory()
  const { t } = useTranslation('checkout')
  const { checkout, setCheckout } = useContext(checkoutContext)
  const { setBasket } = useContext(basketContext)

  const { mutateAsync: mutateAddOrder, isLoading } = useMutation(addOrder)
  const { mutateAsync: mutateTriggerNewOrder } = useMutation(triggerNewOrder)

  const onSubmit = async (form: FormValues) => {
    const now = Timestamp.fromDate(new Date())

    const updatedCheckout: Omit<Order, 'id'> = {
      total: checkout.total,
      shipping: checkout.shipping ?? null,
      deliveryType: checkout.deliveryType ?? null,
      contactDetails: checkout.contactDetails ?? null,
      customerType: checkout.customerType ?? null,
      products: checkout.products ?? null,
      paymentType: form.paymentType ?? null,
      createdAt: now,
      modifiedAt: now,
      status: 'OPEN'
    }

    const response = await mutateAddOrder(updatedCheckout)
    const locationState: CheckoutResultLocationState = {
      orderID: response.id
    }

    history.push(CHECKOUT_RESULT, locationState)

    mutateTriggerNewOrder({
      order: updatedCheckout,
      orderID: response.id
    })

    setCheckout(initState)
    setBasket([])
  }

  const initialValues: FormValues = {
    paymentType: checkout.paymentType || 'TRANSFER'
  }

  const schema = object().shape({
    paymentType: mixed().oneOf(
      [...PAYMENT_METHODS],
      t('checkoutPayment.validation.paymentType.oneOf')
    )
  })

  return {
    onSubmit,
    initialValues,
    schema,
    isLoading
  }
}

export type CheckoutResultLocationState = { orderID: string }

export default useFormSubmit
