import { IconName } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Box, Flexbox, Tile as TileComponent } from 'components'
import React, { ReactNode } from 'react'

type Props = {
  icon?: IconName
  title: string
  content: ReactNode
  extended?: boolean
}

const Tile = ({ extended, content, icon, title }: Props) => (
  <TileComponent
    gridColumn={extended ? '1 / span 2' : 'unset'}
    flexDirection="column"
  >
    <Flexbox alignItems="center" marginBottom="m-size">
      {icon && <FontAwesomeIcon size="2x" icon={icon} />}

      <Box marginLeft={icon && 'm-size'} as="h2" marginY="0">
        {title}
      </Box>
    </Flexbox>

    {content}
  </TileComponent>
)

export default Tile
