/* eslint-disable no-use-before-define */
/* eslint-disable operator-linebreak */
/* eslint-disable indent */
/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-nested-ternary */
/* eslint-disable no-confusing-arrow */
import { Flexbox } from 'components'
import {
  CHECKOUT_DELIVERY,
  CHECKOUT_DETAILS,
  CHECKOUT_PAYMENT,
  CHECKOUT_PRODUCTS,
  CHECKOUT_SHIPPING
} from 'constants/routes'
import changeColorAlpha from 'old/styles/changeColorAlpha'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { useLocation } from 'react-router-dom'
import styled from 'styled-components'

const StepTracker = () => {
  const { t } = useTranslation('checkout')
  const current = useCheckoutStep()

  return (
    <Flexbox justifyContent="center" marginBottom="3rem">
      <Wrapper>
        {[1, 2, 3, 4, 5].map((step) => {
          if (step === 4) {
            return null
          }

          return (
            <Flexbox
              key={step}
              justifyContent="center"
              className={getClassName(current, step)}
            >
              <Flexbox
                width="10rem"
                justifyContent="center"
                alignItems="center"
                flexDirection="column"
                zIndex={1}
              >
                <StepIcon className="checkout-header-step">
                  {step === 5 ? 4 : step}
                </StepIcon>

                <h6 className="checkout-header-subtitle">
                  {t(`steps.${step}`)}
                </h6>
              </Flexbox>
            </Flexbox>
          )
        })}
      </Wrapper>
    </Flexbox>
  )
}

const Wrapper = styled.ul`
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-items: flex-start;
  padding: 0;
  margin: auto;
  position: relative;

  :before {
    content: '';
    position: absolute;
    top: 15px;
    left: 0;
    right: 0;
    margin: auto;
    width: 85%;
    height: 3px;
    background: ${(props) =>
      `${changeColorAlpha(props.theme.colors['border-color'], 0.06)}`};
  }
`
const getClassName = (current: number, step: number) => {
  if (current === step || (current === 4 && step === 3)) {
    return 'is-active-step'
  }

  if (step < current) {
    return 'is-done-step'
  }

  return ''
}

export const useCheckoutStep = () => {
  const { pathname } = useLocation()

  let current = 0

  if (pathname.includes(CHECKOUT_PRODUCTS)) {
    current = 1
  } else if (pathname.includes(CHECKOUT_DETAILS)) {
    current = 2
  } else if (pathname.includes(CHECKOUT_DELIVERY)) {
    current = 3
  } else if (pathname.includes(CHECKOUT_SHIPPING)) {
    current = 4
  } else if (pathname.includes(CHECKOUT_PAYMENT)) {
    current = 5
  }

  return current
}

const StepIcon = styled.h4`
  margin: 0;
  padding: ${(props) => props.theme.space['m-size']};
  background: ${(props) => props.theme.colors['border-color']};
  border-radius: 50%;
  width: 3px;
  height: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: ${(props) => props.theme.colors['gray-10']};
`

export default StepTracker
