/* eslint-disable no-unused-vars */
/* eslint-disable no-undef */
import { useHistory } from 'react-router-dom'
import { object, mixed } from 'yup'
import { CHECKOUT_PAYMENT, CHECKOUT_SHIPPING } from 'constants/routes'
import { useTranslation } from 'react-i18next'
import { useContext } from 'react'
import { checkoutContext } from 'providers/CheckoutProvider'
import add from 'lodash.add'
import { DeliveryType } from 'models/order'
import { DELIVERY_METHODS } from 'cms/config'

export type FormValues = {
  deliveryType?: DeliveryType
}

const useForm = () => {
  const { t } = useTranslation('checkout')
  const history = useHistory()

  const { checkout, setCheckout } = useContext(checkoutContext)

  const onSubmitForm = (form: FormValues) => {
    const deliveryPrice =
      DELIVERY_METHODS.find((method) => method.name === form.deliveryType)
        ?.price || 0

    if (form.deliveryType) {
      setCheckout((prev) => ({
        ...prev,
        deliveryType: form.deliveryType,
        total: {
          ...prev.total,
          delivery: deliveryPrice,
          sum: add(prev.total.products, deliveryPrice)
        }
      }))
    }

    history.push(
      form.deliveryType === 'FREE_PARCEL' ? CHECKOUT_SHIPPING : CHECKOUT_PAYMENT
    )
  }

  const initialValues: FormValues = {
    deliveryType: checkout.deliveryType
  }

  const schema = object().shape({
    deliveryType: mixed().oneOf(
      DELIVERY_METHODS.map((method) => method.name),
      t('checkoutDelivery.validation.deliveryType.oneOf')
    )
  })

  return {
    onSubmitForm,
    initialValues,
    schema
  }
}

export default useForm
