import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import { Formik, Form as FormikForm } from 'formik'
import { Redirect } from 'react-router-dom'
import { Page } from 'components'
import { CHECKOUT_DETAILS } from 'constants/routes'
import { checkoutContext } from 'providers/CheckoutProvider'
import { DELIVERY_METHODS } from 'cms/config'
import Actions from '../common/Actions'
import { StepTracker, Wrapper, Total, StepTitle } from '../common'

import useForm from './useForm'
import Form from './Form'

const CheckoutDelivery = () => {
  const { t } = useTranslation('checkout')

  useDocumentTitle(t('checkoutDelivery.tabTitle'))
  useScrollTop()

  const { checkout } = useContext(checkoutContext)

  const { schema, onSubmitForm, initialValues } = useForm()

  if (!checkout.contactDetails) {
    return <Redirect to={CHECKOUT_DETAILS} />
  }

  return (
    <Page>
      <StepTracker />

      <Wrapper>
        <StepTitle>{t('checkoutDelivery.title')}</StepTitle>

        <Formik
          initialValues={initialValues}
          validateOnChange
          validationSchema={schema}
          onSubmit={onSubmitForm}
        >
          {({ handleSubmit, values }) => {
            const delivery = DELIVERY_METHODS.find(
              (method) => method.name === values.deliveryType
            )

            return (
              <FormikForm onSubmit={handleSubmit}>
                <Form />

                <Total customDelivery={delivery?.price} />

                <Actions />
              </FormikForm>
            )
          }}
        </Formik>
      </Wrapper>
    </Page>
  )
}

export default CheckoutDelivery
