/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-use-before-define */
import { useTranslation } from 'react-i18next'
import React, { useContext } from 'react'
import { Formik, Form as FormikForm, Field, FieldProps } from 'formik'
import { Product } from 'models/product'
import useForm, { FormValues } from './useForm'
import styled from 'styled-components'
import { basketContext } from 'providers/BasketProvider'
import { SubmitButton } from '../common'
import { SelectOption, Variant } from 'old/types'
import { Select } from 'components'

type Props = {
  product: Product
  onSelect: (value?: string) => void
}

const Form = ({ product, onSelect }: Props) => {
  const { t } = useTranslation('viewProduct')

  const { basket } = useContext(basketContext)
  const { schema, initialValues, submitForm } = useForm(product)

  if (!product.variants)
    throw new Error(`No variants in product with type: ${product.type}`)

  return (
    <Formik
      initialValues={initialValues}
      validateOnChange
      validationSchema={schema}
      onSubmit={submitForm}
    >
      {({ values }) => {
        const { variant } = values

        const isInBasket = basket.find(
          (item) => product.id === item.id && variant === item.variant
        )

        return (
          <FormikForm>
            <Field name="variant">
              {(fieldProps: FieldProps<Variant, FormValues>) => (
                <>
                  <VariantLabel>{product.variantsLabel}</VariantLabel>

                  <Select
                    placeholder={t('selectPlaceholder')}
                    onChange={(option) => {
                      fieldProps.form.setFieldValue(
                        fieldProps.field.name,
                        option?.value
                      )
                      onSelect(option?.value)
                    }}
                    options={product.variants}
                  />
                </>
              )}
            </Field>

            <SubmitButton
              separation
              disabled={!variant}
              isInBasket={!!isInBasket}
            />
          </FormikForm>
        )
      }}
    </Formik>
  )
}

const sortAndMapOptions = (options?: SelectOption[]) =>
  options
    ?.sort((a, b) => (a < b ? -1 : 1))
    .map((option) => ({
      label: option.label,
      value: option.value
    }))

const VariantLabel = styled.h5`
  margin: 0;
  margin-bottom: 1rem;
  color: ${(props) => props.theme.colors['gray-10']};
`

export default Form
