/* eslint-disable no-use-before-define */
import { basketContext } from 'providers/BasketProvider'
import React, { useContext } from 'react'
import { useLocation } from 'react-router-dom'
import styled from 'styled-components'
import { Badge } from 'components'
import { basketToggleContext } from 'providers/BasketToggleProvider'
import {
  CHECKOUT_PAYMENT,
  CHECKOUT_DELIVERY,
  CHECKOUT_DETAILS,
  CHECKOUT_PRODUCTS,
  CHECKOUT_SHIPPING,
  CHECKOUT_RESULT
} from 'constants/routes'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const BasketToggle = () => {
  const { pathname } = useLocation()
  const { basket } = useContext(basketContext)
  const { setIsOpen } = useContext(basketToggleContext)
  return (
    <Container
      className="button-link navigation-menu-link basket-toggle"
      disabled={basketDisabledpathnames.includes(pathname)}
      onClick={() => setIsOpen((prev) => !prev)}
      type="button"
    >
      <Badge count={basket.length}>
        <FontAwesomeIcon icon="shopping-basket" size="2x" />
      </Badge>
    </Container>
  )
}

const Container = styled.button``

const basketDisabledpathnames = [
  CHECKOUT_PRODUCTS,
  CHECKOUT_DETAILS,
  CHECKOUT_DELIVERY,
  CHECKOUT_SHIPPING,
  CHECKOUT_PAYMENT,
  CHECKOUT_RESULT
]

export default BasketToggle
