import { createContext, ReactNode, useState } from 'react'
import React from 'react'
import { Order } from 'models/order'

type Props = {
  children: ReactNode
}

export type Checkout = Omit<Order, 'id' | 'createdAt' | 'status' | 'modifiedAt'>

export type CheckoutContent = {
  checkout: Checkout
  setCheckout: React.Dispatch<React.SetStateAction<Checkout>>
}

export const initState: Checkout = {
  total: {
    products: 0,
    delivery: 0,
    sum: 0
  },
  paymentType: null,
  products: null,
  customerType: null,
  contactDetails: null,
  deliveryType: null,
  shipping: null
}

export const checkoutContext = createContext<CheckoutContent>({
  checkout: initState,
  setCheckout: () => undefined
})

const CheckoutProvider = ({ children }: Props) => {
  const [checkout, setCheckout] = useState(initState)

  return (
    <checkoutContext.Provider value={{ checkout, setCheckout }}>
      {children}
    </checkoutContext.Provider>
  )
}

export default CheckoutProvider
