import { Button } from 'components'
import React from 'react'
import { useTranslation } from 'react-i18next'

type Props = {
  isInBasket: boolean
  disabled?: boolean
  separation?: boolean
}

const SubmitButton = ({ separation, disabled, isInBasket }: Props) => {
  const { t } = useTranslation('viewProduct')

  return (
    <Button
      marginTop={separation ? '2rem' : 0}
      type="submit"
      disabled={disabled}
      variant={isInBasket ? 'secondary' : 'primary'}
    >
      {isInBasket ? t('actions.removeFromBasket') : t('actions.addToBasket')}
    </Button>
  )
}

export default SubmitButton
