import Button from 'components/Button'
import { Box, Flexbox, Tile, Page } from 'components'
import { PRODUCTS, HOME } from 'constants/routes'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { Redirect, useHistory, useLocation } from 'react-router-dom'
import { useDocumentTitle, useScrollTop } from 'hooks'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useTheme } from 'styled-components'
import { StepTitle, Wrapper } from '../common'
import { CheckoutResultLocationState } from '../CheckoutPayment/useForm'

const CheckoutResult = () => {
  const { state } = useLocation<CheckoutResultLocationState>()

  const { t } = useTranslation('checkout')

  useDocumentTitle(t('checkoutResult.tabTitle'))
  useScrollTop()

  const history = useHistory()

  if (!state?.orderID) {
    return <Redirect to={HOME} />
  }

  const alfa = useTheme()

  return (
    <Page>
      <Wrapper>
        <Tile>
          <Flexbox marginTop="3rem" justifyContent="center">
            <FontAwesomeIcon
              color={alfa.colors.green}
              size="5x"
              icon="check-circle"
            />
          </Flexbox>

          <StepTitle>{t('checkoutResult.title')}</StepTitle>

          <Flexbox
            marginTop="1rem"
            alignItems="center"
            flexDirection="column"
            justifyContent="center"
          >
            <Box as="p" marginBottom="0">
              {t('checkoutResult.orderId')}
            </Box>
            <h3>{state.orderID}</h3>
          </Flexbox>

          <Flexbox
            textAlign="center"
            justifyContent="center"
            as="p"
            marginBottom="0"
          >
            {t('checkoutResult.info')}
          </Flexbox>

          <Flexbox
            marginTop="5rem"
            flexDirection="row-reverse"
            justifyContent="space-between"
          >
            <Button variant="primary" onClick={() => history.push(PRODUCTS)}>
              {t('checkoutResult.actions.backToShop')}
            </Button>
            <Button variant="secondary" onClick={() => history.push(HOME)}>
              {t('checkoutResult.actions.backToHome')}
            </Button>
          </Flexbox>
        </Tile>
      </Wrapper>
    </Page>
  )
}

export default CheckoutResult
