import { Flexbox } from 'components'
import { displayMoney } from 'utils'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import { checkoutContext } from 'providers/CheckoutProvider'
import add from 'lodash.add'

type Props = {
  customProducts?: number
  customDelivery?: number
}

const Total = ({ customProducts, customDelivery }: Props) => {
  const { t } = useTranslation('checkout')

  const { checkout } = useContext(checkoutContext)
  const { total } = checkout

  const products = customProducts || total.products
  const delivery = customDelivery || total.delivery
  const sum = add(products, delivery)

  return (
    <Flexbox marginTop="2rem" justifyContent="flex-end" alignItems="center">
      <table>
        <tbody>
          {
            <tr>
              <td>
                <span className="d-block margin-0 padding-right-s text-right">
                  {t('total.products')}
                </span>
              </td>
              <td>
                <h4 className="basket-total-amount text-subtle text-right margin-0">
                  {displayMoney(products)}
                </h4>
              </td>
            </tr>
          }

          {
            <tr>
              <td>
                <span className="d-block margin-0 padding-right-s text-right">
                  {t('total.delivery')}
                </span>
              </td>
              <td>
                <h4 className="basket-total-amount text-subtle text-right margin-0 ">
                  {displayMoney(delivery)}
                </h4>
              </td>
            </tr>
          }

          {
            <tr>
              <td>
                <span className="d-block margin-0 padding-right-s text-right">
                  {t('total.sum')}
                </span>
              </td>
              <td>
                <h2 className="basket-total-amount text-right">
                  {displayMoney(sum)}
                </h2>
              </td>
            </tr>
          }
        </tbody>
      </table>
    </Flexbox>
  )
}

export default Total
