/* eslint-disable no-use-before-define */
/* eslint-disable no-unused-expressions */
import { ImageLoader, Box, Badge, Flexbox, Tile } from 'components'

import React, { useContext } from 'react'

import styled from 'styled-components'

import { generatePath, Link } from 'react-router-dom'

import { basketContext } from 'providers/BasketProvider'
import { useQuery } from 'react-query'
import { getProduct } from 'api'
import QueryLoader from 'components/QueryLoader'
import { displayMoney } from 'utils'
import { BasketItem as BasketItemType } from 'models/basketItem'
import { VIEW_PRODUCT } from 'constants/routes'
import { basketToggleContext } from 'providers/BasketToggleProvider'
import { checkoutContext } from 'providers/CheckoutProvider'
import add from 'lodash.add'
import subtract from 'lodash.subtract'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import SimpleButton from './SimpleButton'

type Props = {
  product: BasketItemType
}

const BasketItem = ({ product: basketProduct }: Props) => {
  const { setBasket } = useContext(basketContext)
  const { closeBasket } = useContext(basketToggleContext)
  const { setCheckout } = useContext(checkoutContext)

  const { id, variant, quantity } = basketProduct

  const productQuery = useQuery(`product/${id}`, () => getProduct({ id }))

  const productPath = generatePath(VIEW_PRODUCT, { id })

  const addQuantity = () => {
    setBasket((prev) => {
      const index = prev.findIndex((elem) => {
        if (variant) {
          return elem.id === id && elem.variant === variant
        }
        return elem.id === id
      })
      const result = [...prev]

      result[index].quantity = quantity + 1
      return result
    })

    setCheckout((checkoutPrev) => ({
      ...checkoutPrev,
      total: {
        ...checkoutPrev.total,
        products: add(checkoutPrev.total.products, basketProduct.price),
        sum: add(checkoutPrev.total.sum, basketProduct.price)
      }
    }))
  }

  const subtractQuantity = () => {
    setBasket((prev) => {
      const index = prev.findIndex((elem) => {
        if (variant) {
          return elem.id === id && elem.variant === variant
        }
        return elem.id === id
      })
      const result = [...prev]

      result[index].quantity = quantity - 1
      return result
    })

    setCheckout((checkoutPrev) => ({
      ...checkoutPrev,
      total: {
        ...checkoutPrev.total,
        products: subtract(checkoutPrev.total.products, basketProduct.price),
        sum: subtract(checkoutPrev.total.sum, basketProduct.price)
      }
    }))
  }

  const onRemoveFromBasket = () => {
    setBasket((prev) => {
      const productInBasket = findBasketItem(prev, basketProduct).product

      return prev.filter((elem) => elem !== productInBasket)
    })

    setCheckout((checkoutPrev) => {
      const allProductsPrice = basketProduct.price * basketProduct.quantity

      return {
        ...checkoutPrev,
        total: {
          ...checkoutPrev.total,
          products: subtract(checkoutPrev.total.products, allProductsPrice),
          sum: subtract(checkoutPrev.total.sum, allProductsPrice)
        }
      }
    })
  }

  return (
    <Container>
      <QueryLoader query={productQuery}>
        {(product) => {
          let variantName = product.variants?.find(
            (elem) => elem.value === variant
          )?.label

          if (product.type === 'CUSTOM') {
            variantName = variant
          }

          const thumbnail =
            product.gallery.find((image) => image.variant === variant)?.url ||
            product.gallery[0].url

          return (
            <>
              <SimpleButton
                disabled={quantity === 1}
                gridArea="button-minus"
                onClick={subtractQuantity}
              >
                <FontAwesomeIcon icon="minus" size="1x" />
              </SimpleButton>

              <SimpleButton gridArea="button-plus" onClick={addQuantity}>
                <FontAwesomeIcon icon="plus" size="1x" />
              </SimpleButton>

              <Box width="2rem" height="2rem" gridArea="quantity">
                <Badge stanalone count={quantity} />
              </Box>

              <Box
                gridArea="picture"
                position="relative"
                width="9rem"
                height="9rem"
              >
                <ImageLoader
                  alt={product.name}
                  className="basket-item-img"
                  src={thumbnail}
                />
              </Box>

              <Flexbox flexDirection="column" gridArea="name">
                <Link to={productPath} onClick={closeBasket}>
                  {product.name}
                </Link>

                {variantName && (
                  <Box as="span" marginTop="0.5rem" className="spec-title">
                    {variantName}
                  </Box>
                )}
              </Flexbox>

              <Flexbox alignItems="center">
                <h4 className="my-0">{displayMoney(product.price)}</h4>
              </Flexbox>

              <SimpleButton
                gridArea="button-remove"
                onClick={onRemoveFromBasket}
              >
                <FontAwesomeIcon icon="times" size="1x" />
              </SimpleButton>
            </>
          )
        }}
      </QueryLoader>
    </Container>
  )
}

const findBasketItem = (basket: BasketItemType[], product: BasketItemType) => {
  const index = basket.findIndex((elem) => {
    if (product.variant) {
      return elem.id === product.id && elem.variant === product.variant
    }
    return elem.id === product.id
  })

  return {
    product: basket[index],
    index
  }
}

const Container = styled(Tile)`
  padding: ${(props) => props.theme.space['s-size']};
  min-height: 11.6rem;
  display: flex;
  align-items: center;
  /* @include bezier-transition(); */
  /* animation: slide-up 0.5s ease; */

  /* max-width: 60rem; */
  width: 100%;
  margin-bottom: ${(props) => props.theme.space['m-size']};

  display: grid;
  grid-template-columns: 3.5rem 2rem 9rem auto 3.5rem;
  grid-gap: 0.5rem 2rem;
  grid-template-areas:
    'button-plus quantity picture name button-remove'
    'button-minus quantity picture alba button-remove';
`

export default BasketItem
