import Flexbox from 'components/Flexbox'
import React, { ReactNode } from 'react'
import styled from 'styled-components'

type Props = {
  count: number
  children?: ReactNode
  stanalone?: boolean
}

const Badge = ({ count, children, stanalone = false }: Props) => (
  <Flexbox
    alignItems="center"
    justifyContent="center"
    position="relative"
    className="badge"
  >
    {children}

    {count >= 1 && <Count stanalone={stanalone}>{count}</Count>}
  </Flexbox>
)

type CountProps = {
  stanalone: boolean
}
const Count = styled.span<CountProps>`
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background: ${(props) => props.theme.colors.red};
  display: flex;
  justify-content: center;
  align-items: center;
  color: ${(props) => props.theme.colors.white};
  font-size: 1.1rem;
  font-weight: bold;
  position: ${(props) => !props.stanalone && 'absolute'};
  top: ${(props) => !props.stanalone && '-1.2rem'};
  right: ${(props) => !props.stanalone && '-1.5rem'};
`

export default Badge
