import { Box } from 'components'
import { Flexbox } from 'components'
import { FieldProps } from 'formik'
import React from 'react'

type Props = FieldProps & {
  id: string
  isFirst: boolean
  title: string
  subtitle?: string
  val?: string
  fieldLabel: string
}

const CheckboxField = ({
  id,
  field: { value, onChange, onBlur, name },
  meta: { touched, error },
  isFirst,
  title,
  subtitle,
  val,
  fieldLabel
}: Props) => {
  return (
    <>
      {isFirst && (
        <Flexbox
          marginBottom="0 !important"
          marginRight="auto"
          justifyContent="flex-start"
        >
          {touched && error ? (
            <span className="label-input label-error">{error}</span>
          ) : (
            // eslint-disable-next-line jsx-a11y/label-has-associated-control
            <label className="label-input" htmlFor={name}>
              {fieldLabel}
            </label>
          )}
        </Flexbox>
      )}

      <div className="checkout-checkbox-field">
        <input
          name={name}
          id={id}
          type="radio"
          value={id}
          checked={id === value}
          onChange={onChange}
          onBlur={onBlur}
        />
        <label className="d-flex w-100" htmlFor={id}>
          <h5 className="d-flex-grow-1 margin-0">
            {title}

            {subtitle && (
              <Box marginLeft="0.5rem" as="span" className="text-subtle">
                {subtitle}
              </Box>
            )}
          </h5>

          {val && <h4 className="margin-0">{val}</h4>}
        </label>
      </div>
    </>
  )
}

export default CheckboxField
