/* eslint-disable no-use-before-define */
import { getOrderStatus } from 'api'
import { OrderStatus } from 'models/order'
import { Email } from 'old/types'
import { loaderContext } from 'providers/LoaderProvider'
import { useContext, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useMutation } from 'react-query'
import { object, string } from 'yup'

export type FormValues = {
  id: string
}

const useForm = () => {
  const { t } = useTranslation('contact')
  const [view, setView] = useState<View>({ view: 'FORM' })

  const { mutateAsync: mutateGetOrderStatus } = useMutation(getOrderStatus)

  const initialValues = {
    id: ''
  }

  const { show, hide } = useContext(loaderContext)

  const schema = object({
    id: string()
      .required(t('items.CHECK_STATUS.form.id.validation.required'))
      .length(20, t('items.CHECK_STATUS.form.id.validation.length'))
  })

  const onSubmit = async (formValues: FormValues) => {
    try {
      show()
      const order = await mutateGetOrderStatus(formValues)
      setView({ view: 'RESULT', status: 'AWAITING_FOR_PICKUP' })
      console.log('order', order)
    } catch (e) {
      setView({ view: 'ERROR', message: t('items.CHECK_STATUS.error.info') })
      console.log('error', e)
    }
    hide()
  }

  return {
    initialValues,
    schema,
    onSubmit,
    view,
    setView
  }
}

export type View =
  | { view: 'FORM' }
  | { view: 'RESULT'; status: OrderStatus }
  | { view: 'ERROR'; message: string }

export default useForm
