/* eslint-disable implicit-arrow-linebreak */
import { Box, Flexbox, Logo } from 'components'
import {
  CONTACT,
  DELIVERY_AND_PAYMENTS,
  HOME,
  PRODUCTS,
  VIEW_PRODUCT
} from 'constants/routes'
import React from 'react'
import { matchPath, useLocation } from 'react-router-dom'

const Footer = () => {
  const { pathname } = useLocation()

  const visibleOnlyPath: string[] = [
    HOME,
    PRODUCTS,
    VIEW_PRODUCT,
    DELIVERY_AND_PAYMENTS,
    CONTACT
  ]

  const matchRoute = matchPath(pathname, {
    path: visibleOnlyPath,
    exact: true,
    strict: false
  })

  if (!matchRoute) {
    return null
  }

  return (
    <Flexbox
      paddingX="xxl-size"
      paddingY="l-size"
      backgroundColor="off-white"
      justifyContent="center"
      alignItems="center"
    >
      <Flexbox
        flexDirection="column"
        alignItems="center"
        justifyContent="center"
      >
        <Logo />
        <Box marginTop="l-size">{new Date().getFullYear()}</Box>
      </Flexbox>
    </Flexbox>
  )
}

export default Footer
