import { Product } from 'models/product'
import { basketContext } from 'providers/BasketProvider'
import { useContext } from 'react'
import { string, object } from 'yup'
import add from 'lodash.add'
import subtract from 'lodash.subtract'
import { checkoutContext } from 'providers/CheckoutProvider'
import { Variant } from 'old/types'

export type FormValues = {
  variant: Variant
}

const useForm = (product: Product) => {
  const { basket, setBasket } = useContext(basketContext)
  const { setCheckout } = useContext(checkoutContext)

  const initialValues = {
    variant: ''
  }

  const schema = object({
    variant: string()
  })

  const submitForm = (formValues: FormValues) => {
    const { variant } = formValues
    const isInBasket = basket.find(
      (item) => product.id === item.id && variant === item.variant
    )

    setBasket((prev) => {
      if (isInBasket) {
        const productInBasket = prev.find(
          (elem) => product.id === elem.id && variant === elem.variant
        )

        return prev.filter((elem) => elem !== productInBasket)
      }

      const newBasketProduct = {
        id: product.id,
        quantity: 1,
        variant,
        price: product.price
      }
      return [...prev, newBasketProduct]
    })

    setCheckout((prev) => {
      if (isInBasket) {
        return {
          ...prev,
          total: {
            ...prev.total,
            products: subtract(prev.total.products, product.price),
            sum: subtract(prev.total.products, product.price)
          }
        }
      }

      return {
        ...prev,
        total: {
          ...prev.total,
          products: add(prev.total.products, product.price),
          sum: subtract(prev.total.products, product.price)
        }
      }
    })
  }

  return {
    initialValues,
    schema,
    submitForm
  }
}

export default useForm
