import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import styled from 'styled-components'
import { useQuery } from 'react-query'
import { QueryLoader, Page, Separator, Flexbox, Tile } from 'components'
import { getProduct } from 'api'
import { useDocumentTitle, useScrollTop } from 'hooks'

import { displayMoney } from 'utils'
import { useTranslation } from 'react-i18next'
import BackButton from './BackButton'
import Images from './Images'
import Recommended from './Recommended'
import NoTypeForm from './NoType'
import ListTypeForm from './ListType'
import CustomTypeForm from './CustomType'

const ViewProduct = () => {
  const { id } = useParams<{ id: string }>()
  const { t } = useTranslation('viewProduct')

  const productQuery = useQuery(`product/${id}`, () => getProduct({ id }))

  useScrollTop(id)
  useDocumentTitle(t('tabTitle', { productName: productQuery.data?.name }))

  const [variant, setVariant] = useState<string>()

  useEffect(() => setVariant(undefined), [id])

  return (
    <Page>
      <QueryLoader query={productQuery}>
        {(product) => {
          let form = <NoTypeForm product={product} />

          if (product.type === 'LIST') {
            form = (
              <ListTypeForm
                onSelect={(value) => setVariant(value)}
                product={product}
              />
            )
          }

          if (product.type === 'CUSTOM') {
            form = (
              <CustomTypeForm
                onSelect={(value) => setVariant(value)}
                product={product}
              />
            )
          }

          return (
            <>
              <BackButton />

              <Tile flexDirection="row">
                <Images variant={variant} product={product} />

                <Flexbox flexGrow={1} flexDirection="column" marginLeft="3rem">
                  <ProductName>{product.name}</ProductName>

                  <Separator />

                  <Price>{displayMoney(product.price)}</Price>
                  <PriceSubtext>{t('priceSubtitle')}</PriceSubtext>

                  <Separator />

                  <Description>{product.description}</Description>

                  <Separator />

                  {form}
                </Flexbox>
              </Tile>
            </>
          )
        }}
      </QueryLoader>

      {productQuery.isFetched && <Recommended />}
    </Page>
  )
}

const ProductName = styled.h1`
  margin: 0;
`

const Price = styled.h2`
  font-size-adjust: 1.2;
  margin: 0;
`

const PriceSubtext = styled.h4`
  margin: 0;
  margin-top: 0.5rem;
  color: ${(props) => props.theme.colors['gray-10']};
`

const Description = styled.p`
  margin: 0;
`

export default ViewProduct
